/* ==========
FOLIO INTRO
========== */

// Landing page
.folio-landing {
    @include flex-column;
    justify-content: center;
    align-items:center;

    background-color:$primary-1; 
    width:calc(100vw - 16rem); 
    height:calc(100vh - 8rem); 
    margin:4rem 8rem;

    .folio-intro {
        color:#fff; 
        width:100%;
        max-width:76rem; 

        .text {
            width:100%;
            max-width:64rem;
            margin:auto;
        }
    
        p:first-child {
            font-size:2.4rem;
            line-height:1.6;
        }

        p {
            font-size:1.6rem;
            line-height:1.6;
        }
    } 

} // close folio-landing


// Case studies
.folio-landing-link {
    @include flex-row;
    justify-content: center;

    width:calc(100vw - 16rem); 
    height:calc(100vh - 16rem); 
    margin:8rem;

    figure {
        margin:0;
    }

    .imagery,
    .content {
        flex-grow: 1;
        flex-basis: 1;
        width:100%;
        height:100%;
        position:relative;
    }

    .content {
        padding: 4rem 4rem 0 0;
    }

    .label-title {
        margin:0;
    }

    .title {
        margin:1.2rem 0;
    }

}  // close folio-landing-link


.folio-landing-link.link-iep {
    
    .screen01 {
        overflow: hidden;
        position: absolute;
        top: 4rem;
        left: 8%;
        width: 84%;
        z-index: 1;
        height: 34rem;
        border-radius: .4rem;
        box-shadow: 0.2rem 0.2rem 1.2rem 0 rgba(0, 0, 0, 0.2);

        img {
            width:100%;
        }
    }

    .thumb01 {
        overflow: hidden;
        position:absolute;
        top: 36rem;
        right: 4rem;
        z-index:0;
        width: 28rem;
        height: 14rem;
        border-radius: .4rem;

        img {
            width:100%;
        }
    }

    background: rgb(248,255,255);
    background: radial-gradient(circle, rgba(248,255,255,1) 0%, rgba(235,254,255,1) 100%); 

}

.folio-landing-link.link-ecomm {

    background: rgb(101,111,124);
    background: radial-gradient(circle, rgba(101,111,124,1) 0%, rgba(69,75,83,1) 100%); 

}


.folio-landing-link.link-search {

    background: rgb(234,245,255);
    background: radial-gradient(circle, rgba(234,245,255,1) 0%, rgba(196,231,255,1) 100%); 

}

@media screen and (max-height: 50rem) {

    .folio-landing,
    .folio-landing-link {
        height:unset;
    }
    
}

